erRadius: 12px;\n                width: 350px;\n                max-width: 100%;\n                margin: 12px;\n                border-radius: $borderRadius;\n                overflow: hidden;\n                \n                .cardHeader {\n                    @include flexRow;\n                    $headerXPadding: 16px;\n                    padding: 4px $headerXPadding;\n                    position: relative;\n                    cursor: pointer;\n                    &:hover {\n                        filter: brightness(120%);\n                    }\n                    .tournamentName {\n                        margin-right: 10px;\n                    }\n                    .selectedUtl {\n                        position: absolute;\n                        width: 100%;\n                        height: 100%;\n                        margin-right: -$headerXPadding;\n                        border-radius: $borderRadius $borderRadius 0px 0px;\n                        border: 4px solid rgb(0 0 0 / 30%);\n                    }\n                }\n                .cardBody {\n                    padding: 12px;\n                    $nameWidth: 90px;\n                    .utlAttribute {\n                        font-size: 14px;\n                        margin-bottom: 16px;\n                        .attributeName {\n                            display: inline-block;\n                            width: $nameWidth;\n                            font-weight: 700;\n                        }\n                        .value {\n                            width: calc(100% - $nameWidth);\n                            display: inline-flex;\n                        }\n                    }\n                    .linksContainer {\n                        @include centerContainer;\n                        flex-direction: column;\n                        .takanonLink {\n                            margin-top: 8px;\n                        }\n                    }\n                    